<template lang="html">
  <form class="form col-12">
    <div>
      <label class="input-before_label"></label>
      <input class="input" type="password" v-model.trim="oldPassword" />
    </div>
    <div>
      <label class="input-before_label"></label>
      <input class="input" type="password" v-model.trim="newPassword" />
    </div>
    <div class="form-button_group">
      <button class="btn btn-primary btn-lg btn-block" type="button" @click="submit"></button>
    </div>
  </form>
</template>

<script>
export default {
  data () {
    return {
      oldPassword: '',
      newPassword: ''
    }
  },
  methods: {
    submit () {
      let data = {
        oldPwd: this.oldPassword,
        newPwd: this.newPassword
      }
      if (!data.oldPwd || !data.newPwd) {
        return this.$toast({text: '', type: 'e'})
      }
      this.$store.dispatch('updatePwd', data)
      .then(() => {
        this.$toast({text: ' ', type: 's'})
        setTimeout(() => {
          this.$store.dispatch('signout')
          this.$router.push({
            path: '/login'
          })
        }, 1000)
      })
    }
  }

}
</script>

<style lang="scss">
</style>



