<template>
  <div class="table-responsive">
    <table class="table table-hover table-striped">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody class="table-striped">
        <tr v-for="(item, index) in articleList">
          <td>{{ item.title }}</td>
          <td class="type_td">
            <span v-for="type in item.types">{{ type }}</span>
          </td>
          <td>{{ item.date }}</td>
          <td>
            <button class="btn btn-primary" @click="updateArticle(index)"></button>
            <button class="btn btn-danger" @click="removeArticle(index)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <Pager :limit="limit" ref="pager"></Pager>
  </div>
</template>
<script>
import Pager from 'components/Pager'
import { formatDateTime } from '../../filters/index'

export default {
  data () {
    return {
      limit: 10
    }
  },
  computed: {
    articleList () {
      let newArticleList = []
      if (this.$store.state.articleList) {
        this.$store.state.articleList.forEach((item) => {
          item.date = formatDateTime(new Date(item.date))
          newArticleList.push(item)
        })
      }
      return newArticleList
    }
  },
  created () {
    this.getArticleList(this.$store.state.currPage)
  },
  methods: {
    getArticleList (skip) {
      this.$store.dispatch('getArticleList', {limit: this.limit, skip: skip})
    },
    removeArticle (index) {
      let vm = this
      this.$modal({
        title: '',
        text: '',
        sureFn: function () {
          vm.$store.dispatch('removeArticle', index)
          if (index === 0) {
            let skip = vm.$store.state.currPage - 1
            vm.getArticleList(skip)
            vm.$refs.pager.prev()
          }
        }
      })
    },
    updateArticle (index) {
      this.$store.dispatch('saveUpdateArticle', index)
      this.$router.push({path: '/backend/pulish'})
    }
  },
  components: {
    Pager
  }
}
</script>
<style lang="scss">
  .table {
    width:100%;
    border-collapse:collapse;
    .table-striped {
      tr: {
        outline: none;
      }
      tr:nth-of-type(odd) {
        background:#f9f9f9;
      }
      tr:hover {
        outline:1px solid #5cadff;
      }
      td {
        font-size:16px;
        color:#657180;
        text-align:center;
        padding:8px;
      }
    }
  }
</style>



